import React from 'react';
import { NavLink, Outlet, useNavigate } from 'react-router-dom';
import './style-profilepage.scss';
import { useDispatch } from "react-redux";
import { logout } from '../../../http/reducers/userReducer';
import { useSelector } from "react-redux";
export default function ProfilePage() {
	const setActiveLink = ({ isActive }) => isActive ? "active-link" : '';
	const { currentUser, isLoading } = useSelector(state => state.user);
	const dispatch = useDispatch();
	const navigate = useNavigate();
	const goLogin = () => navigate('/login');
	console.log(currentUser, isLoading);
	return (
		<div className='page-profile profile'>
			<h1 className='profile__title'> </h1>
			<div className='profile__wrapper'>
				<section className='profile__center main-profile'>
					<ul className='main-profile__menu menu-profile'>
						<li className='menu-profile__item'>
							<NavLink className={'menu-profile__link ' + setActiveLink} to='main'></NavLink>
						</li>
						<li className='menu-profile__item'>
							<NavLink className={'menu-profile__link ' + setActiveLink} to='delivery'></NavLink>
						</li>
						<li className='menu-profile__item'>
							<NavLink className={'menu-profile__link ' + setActiveLink} to='orders'></NavLink>
						</li>
						<li className='menu-profile__item'>
							<NavLink className={'menu-profile__link ' + setActiveLink} to='discount'></NavLink>
						</li>
						<li className='menu-profile__item'>
							<NavLink className={'menu-profile__link ' + setActiveLink} to='bonuses'></NavLink>
						</li>
						<li className='menu-profile__item'>
							<NavLink className={'menu-profile__link ' + setActiveLink} to='instructions'></NavLink>
						</li>
						<li className='menu-profile__item'>
							<NavLink className={'menu-profile__link ' + setActiveLink} to='appeals'> </NavLink>
						</li>
					</ul>
					<Outlet />
				</section>

				<section className='profile__mycontact mycontact-profile'>
					{!currentUser.isActivated && <h3 style={{ marginBottom: "20px", color: "red" }}> </h3>}
					<div className='mycontact-profile__name'>{currentUser.surname} {currentUser.name}</div>
					<div className='mycontact-profile__email'>{currentUser.email}</div>
					<div className="mycontact-profile__phone">+7 (900) 000-00-00</div>
					<NavLink className={'mycontact-profile__settings ' + setActiveLink} to='editprofile'> </NavLink>
					<button className='mycontact-profile__logout' onClick={() => { dispatch(logout()); goLogin() }}></button>
				</section>
			</div>
		</div>
	)
}
